import React from 'react';
import { Link } from 'react-router-dom';

import Main from '../layouts/Main';

import Education from '../components/Resume/Education';
import Experience from '../components/Resume/Experience';
import Skills from '../components/Resume/Skills';
import References from '../components/Resume/References';

import degrees from '../data/resume/degrees';
import work from '../data/resume/work';
import { skills, categories } from '../data/resume/skills';

// NOTE: sections are displayed in order defined.
const sections = {
  Download: () => (
    <div className="download-section" style={{ marginBottom: "28px" }}>
      <h3>Download My Resume</h3>
      <p>
        Click the button below to download my resume as a PDF.
      </p>
      <a href="/douglasResume.pdf" target="_blank" rel="noopener noreferrer" className="button">
        Download Resume
      </a>
    </div>
  ),
  Education: () => <Education data={degrees} />,
  Experience: () => <Experience data={work} />,
  Skills: () => <Skills skills={skills} categories={categories} />,
  References: () => <References />,
};

const Resume = () => (
  <Main
    title="Resume"
    description="Ke Xiao's Resume. Passion, Brave, Perseverance. Believe in Tech and Data."
  >
    <article className="post" id="resume">
      <header>
        <div className="title">
          <h2><Link to="resume">Resume</Link></h2>
          <div className="link-container">
            {Object.keys(sections).map((sec) => (
              <h4 key={sec}>
                <a href={`#${sec.toLowerCase()}`}>{sec}</a>
              </h4>))}
          </div>
        </div>
      </header>
      {Object.entries(sections).map(([name, Section]) => (
        <Section key={name} />
      ))}
    </article>
  </Main>
);

export default Resume;
